.header {
  margin-top: 0.5rem;
  padding-left: 10px;
}

@media (max-width: 767px) {
  .header {
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding-left: 0;
  }

  .header ul {
    padding: 0;
  }
}

@media (max-width: 450px) {
  .header ul {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.header ul li {
  border-radius: 3px;
  list-style: none;
  margin: 0;
  padding: 0.5rem 1rem;
}

.header ul li:hover {
  background-color: #444;
}

.active-link {
  border-bottom: 1px solid #fff;
  color: #ffc0cb;
}

.header a:hover {
  text-decoration: none;
}

.header h3 a {
  color: #fff;
}

.header h3 a:hover {
  color: #00bc8c;
}

.header button {
  height: 2.2rem;
  margin-left: 3px;
  margin-top: 1px;
}

@media (max-width: 767px) {
  .header button {
    margin: 0 0 16px;
  }
}

.error-indicator {
  align-items: center;
  color: #c78f22;
  display: flex;
  flex-direction: column;
  left: 50%;
  margin-right: -50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}

.error-indicator img {
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.7rem;
}

.error-indicator button {
  margin-top: 1rem;
}

@keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes lds-double-ring {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

@-webkit-keyframes lds-double-ring_reverse {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}

.lds-css {
  margin: 0 auto;
}

.lds-double-ring {
  height: 77px !important;
  position: relative;
  -webkit-transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
  transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
  width: 77px !important;
}

.lds-double-ring:before,
.lds-double-ring:after {
  -webkit-animation: lds-double-ring 1.5s linear infinite;
  animation: lds-double-ring 1.5s linear infinite;
  border: 8px solid #000;
  border-color: #3d7ec0 transparent #3d7ec0 transparent;
  border-radius: 50%;
  content: '';
  height: 160px;
  left: 20px;
  position: absolute;
  top: 20px;
  width: 160px;
}

.lds-double-ring:after {
  -webkit-animation: lds-double-ring_reverse 1.5s linear infinite;
  animation: lds-double-ring_reverse 1.5s linear infinite;
  border-color: transparent #cc4545 transparent #cc4545;
  height: 140px;
  left: 30px;
  top: 30px;
  width: 140px;
}

.error-button {
  background-color: #d04737;
  border-color: #d04737;
}

.error-button:hover {
  background-color: #e12e1c;
  border-color: #d62c1a;
}

.item-details {
  border: 1px solid #444;
  display: flex;
  flex-direction: row;
  padding: 1rem;
}

.item-details .item-image {
  border-radius: 10px;
  height: 30%;
  width: 30%;
}

@media (max-width: 450px) {
  .item-details .item-image {
    height: 20%;
    width: 20%;
  }
}

.item-details .list-group-item {
  padding: 0.25rem;
}

.item-details .list-group-item .term {
  margin-right: 0.5rem;
}

.item-details button {
  margin-top: 5px;
}

.item-list {
  margin-bottom: 1rem;
}

.item-list .list-group-item {
  cursor: pointer;
}

.item-list .list-group-item:hover {
  background-color: #444;
}

.random-planet {
  border: 1px solid #444;
  display: flex;
  margin-bottom: 1rem;
  padding: 1rem;
}

.random-planet .planet-image {
  border-radius: 10px;
  height: 150px;
  margin-right: 1rem;
  width: 150px;
}

@media (max-width: 450px) {
  .random-planet .planet-image {
    height: 20%;
    width: 20%;
  }
}

.random-planet .list-group {
  margin: 1rem 0 0 1rem;
}

.random-planet .list-group-item {
  padding: 0.25rem;
}

.random-planet .list-group-item .term {
  margin-right: 0.5rem;
}

.button-row {
  margin: 0 0 1rem 0;
}

.button-row button {
  margin-right: 1rem;
}

.stardb-app * {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.stardb-app .row {
  margin-bottom: 1rem;
}

